import React, { useEffect } from 'react';
import { Routes, Route, Navigate, useNavigate, useLocation } from "react-router-dom";
import Perpetuals from '../views/gmx-test/App/App';
import { connectWallet } from './utils/wallet';
import { HOMEPAGE_HREF } from './utils/constants';
import Options from './../views/options/App/App';
import { useLocalStorageByChainId } from './../views/gmx-test/lib/legacy';
import { ARBITRUM } from './../views/options/lib/legacyOptions';
import Terminal from '../views/Terminal/Terminal';
import UserProfile from './../views/Terminal/UserProfile/UserProfile';
import MarketOverview from '../views/Terminal/MarketOverview/MarketOverview';
import Builder from '../views/Terminal/Builder/Builder';


const AppRouter = ({ walletAddress, connectWallet, infoTokens }) => {
	const [path, setPath] = useLocalStorageByChainId(ARBITRUM, "terminal_path", HOMEPAGE_HREF);

	const nav = useNavigate();
	const loc = useLocation();

	useEffect(() => {
		if (loc.pathname === '/terminal') {
			nav(HOMEPAGE_HREF);
		} else {
			switch (loc.pathname) {
				case "/terminal/builder":
					setPath("/terminal/builder");
					break;
			}
		}
	}, [loc.pathname]);
	
	return (
		<Routes>
			<Route
				path="/terminal"
				element={
					<Terminal />
				}
			>
				{/* <Route
					path="perpetuals"
					element={
						<Perpetuals
							connectWallet={connectWallet}
							walletAddress={walletAddress} />
					}
				/> */}
				<Route
					path="positions"
					element={
						<Options
							walletAddress={walletAddress}
						/>
					}
				/>
				<Route
					path="builder"
					element={
						<Builder
							walletAddress={walletAddress}
							infoTokens={infoTokens}
							connectWallet={connectWallet}
						/>
					}
				/>
				{/* <Route
					path="profile"
					element={
						<UserProfile
							walletAddress={walletAddress}
						/>
					}
				/> */}
				{/* <Route
					path="market-overview"
					element={
						<MarketOverview
							walletAddress={walletAddress}
						/>
					}
				/> */}
			</Route>
			<Route
				path="*"
				element={<Navigate to={HOMEPAGE_HREF} replace />}
			/>
		</Routes>
	);
};

export default AppRouter;