import React, { useState } from 'react';
import Tab from '../Tab/Tab';
import TradingView from './TradingView';
import cx from "classnames";
import './ChartSelector.scss';
import { useLocalStorage } from 'react-use';

const ChartSelector = (props) => {
	const {
		CHART_PERIODS,
		period,
		setPeriod,
		candleStatsHtml,
		chartRef,
		chartToken,
	} = props;

	const CHARTS = ["simple", "trading"];
	const CHARTS_LABELS = {
		simple: "Simple chart",
		trading: "Trading View",
	};
	const [chart, setChart] = useLocalStorage("Exchange-chart", "simple");

	const onChartOptionChange = (chart) => {
		setChart(chart);
	}

	const isTrading = chart === "trading";
	
	return (
		<div className={cx("ExchangeChart-bottom App-box App-box-border", isTrading && "ExchangeChart_trading-active")}>
			<Tab
				options={CHARTS}
				optionLabels={CHARTS_LABELS}
				option={chart}
				onChange={onChartOptionChange}
				className={"Exchange-chart-tabs"}
			/>
			<div className='ExchangeChart__simple'>
				<div className="ExchangeChart-bottom-header">
					<div className="ExchangeChart-bottom-controls">
						<Tab options={Object.keys(CHART_PERIODS)} option={period} setOption={setPeriod} />
					</div>
					{candleStatsHtml}
				</div>
				<div className="ExchangeChart-bottom-content" ref={chartRef}></div>
			</div>
			<TradingView
				className={"ExchangeChart__trading"}
				chartToken={chartToken}
			/>
		</div>
	);
};

export default ChartSelector;